export default {
  name: "LoginView",
  data() {
    return {
      email: '',
      password: '',
    }
  },
  methods: {
    async signup() {
      const formData = {
        email: this.email,
        password: this.password
      }
      console.log(formData)
      try {
        await this.$store.dispatch('signup', formData)
        await this.$router.push("/")
        this.$emit('update')
      } catch (e) {
        console.log(e)
        this.$message('Введите правильный адрес электронной почты!')
      }

    },
  },
}
